import { connect } from "react-redux";
import DuplicateRequestDialog from "./DuplicateRequestDialog";
import {
  submitRequest,
  clearDuplicateRequest,
  setSendingRequest,
  clearSelectedSong,
} from "../../store/actions/index";

const mapStateToProps = state => {
  const { results } = state;
  const { duplicateRequest } = results;

  return {
    duplicateRequest
  }
}

const mapDispatchToProps = dispatch => ({
  submitRequest: (singer, history) => {
    dispatch(submitRequest(singer, history))
    dispatch(clearDuplicateRequest());
  },
  cancelRequest: () => {
    dispatch(clearDuplicateRequest());
    dispatch(setSendingRequest(false));
    dispatch(clearSelectedSong());
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(DuplicateRequestDialog);