import React from "react";
import { withRouter } from "react-router-dom";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  DialogContentText
} from '@material-ui/core';

const DuplicateRequestDialog = props => {
  const { duplicateRequest, history, submitRequest, cancelRequest } = props;

  const { singer, Artist, Title, duplicateRequestBySinger } = duplicateRequest || '';

  return (
    <Dialog
      open={Boolean(duplicateRequest)}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title">Duplicate Song Request</DialogTitle>
      <DialogContent>
        <DialogContentText id="alert-dialog-description">
          {`The song ${Title} in the style of ${Artist} has been already been request by ${duplicateRequestBySinger ? singer : 'another singer'}.`}
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={cancelRequest} color="primary" autoFocus>
          Cancel Request
          </Button>
        <Button onClick={() => submitRequest(singer, history)} color="primary">
          Submit Anyway
          </Button>
      </DialogActions>
    </Dialog>
  );
}

export default withRouter(DuplicateRequestDialog);