import { takeLatest, call, put, select } from "redux-saga/effects";
import axios from "axios";
import { CHECK_FOR_DUPLICATE_REQUEST } from "../actionTypes/api";
import { getBaseUrl, getSelectedSong } from "./selectors";
import { setSendingRequest, setDuplicateRequest } from "../actions/index";
import { submitRequest } from './apiSubmitRequest';

function* checkForDuplicateRequest(action) {
  const { singer, history } = action;
  const baseUrl = yield select(getBaseUrl);
  const songObj = yield select(getSelectedSong);

  yield put(setSendingRequest(true));
  const body = {
    ...songObj,
  };

  try {
    const response = yield call(axios.post, `${baseUrl}/new-request/duplicate-check`, body);
    const { data } = response;
    const { duplicate } = data;

    if (!duplicate) {
      yield call(submitRequest, { singer, history });
    } else {
      // trigger duplicate modal
      const { Artist, Title } = songObj;
      const { results } = data;
      let duplicateRequestBySinger = false;

      for (let i = 0; i < results.length; i += 1) {
        const request = results[i];
        const { Singer } = request;
        if (Singer === singer) {
          duplicateRequestBySinger = true;
        }
      }

      yield put(setDuplicateRequest({
        singer,
        Artist,
        Title,
        duplicateRequestBySinger
      }));
    }



  } catch (error) {
    // if the call fails, post the request anyway
    yield call(submitRequest, { singer, history });
  }
}

function* watcherSaga() {
  yield takeLatest(CHECK_FOR_DUPLICATE_REQUEST, checkForDuplicateRequest);
}

export default watcherSaga;
